html,
body {
  height: 100%;
}

body {
  background: #141414;
  font: 12em/1 'Helvetica Neue';
}

.text--line {
  font-size: 1em;
}

@media only screen and (min-width: 64em) {
  .text--line {
    font-size: .75em;
  }
}

svg {
  position: absolute;
  width: 100%;
  height: 100%;
}

.text-copy {
  fill: none;
  stroke: white;
  stroke-dasharray: 2% 8%;
  stroke-width: 3px;
  animation: stroke-offset 6s infinite linear;
}

.text-copy:nth-child(1) {
  stroke: #CFF09E;
  stroke-dashoffset: 2%;
}

.text-copy:nth-child(2) {
  stroke: #A8DBA8;
  stroke-dashoffset: 4%;
}

.text-copy:nth-child(3) {
  stroke: #79BD9A;
  stroke-dashoffset: 6%;
}

.text-copy:nth-child(4) {
  stroke: #3B8686;
  stroke-dashoffset: 8%;
}

.text-copy:nth-child(5) {
  stroke: #0B486B;
  stroke-dashoffset: 10%;
}

.svg-wrapper {
  position: absolute;
  left: 50%;
  bottom: 5%;
  margin: 0 auto;
  width: 320px;
  height: 60px;
  margin: 0 0 0 -160px;
  font-size: 24px;
  color: #CFF09E;
  text-decoration: none;
  font-weight: 300;
  text-align: center;
  vertical-align: middle;
}

.svg-wrapper svg {
  top: 0;
  left: 0;
}

.svg-wrapper:hover .shape {
  stroke-width: 2px;
  stroke-dashoffset: 0;
  stroke-dasharray: 760;
}

.svg-wrapper .shape {
  stroke-dasharray: 140 540;
  stroke-dashoffset: -474;
  stroke-width: 4px;
  fill: transparent;
  stroke: #79BD9A;
  border-bottom: 5px solid black;
  transition: stroke-width 1s, stroke-dashoffset 1s, stroke-dasharray 1s;
}

.svg-wrapper .text {
  font-family: 'Roboto Condensed';
  position: relative;
  top: 20px;
  font-size: 21px;
  letter-spacing: 2px;
  color: #79BD9A;
}

@keyframes stroke-offset {
  50% {
    stroke-dashoffset: 10%;
    stroke-dasharray: 10 25%;
  }
}

